.testimonials{
    display: flex;
    gap: 1rem;
    padding: 0 2rem;
}

.test-left{
    display: flex;
    flex:  1 1;
    gap: 2rem;
    flex-direction: column;
    text-transform: uppercase;
    color: white;
}

.test-left>:nth-child(1){
    font-size: 1.8rem;
    color: var(--orange);
}
.test-left>:nth-child(2),.test-left>:nth-child(3){
    font-weight: bold;
    font-size: 3rem;
}
.test-left>:nth-child(4){
    text-transform: none;
    text-align: justify;
    letter-spacing: 2px;
    line-height: 40px;
}

.test-right{
    flex: 1;
    position: relative;
}

.test-right>img{
    position: absolute;
    width: 17rem;
    height: 20rem;
    object-fit: cover;
    right: 8rem;
    top: 2rem;
}

.test-right>:nth-child(1){
    position: absolute;
    width: 17rem;
    height: 20rem;
    border: 2px solid orange;
    background-color: transparent;
    right: 9rem;
    top: 0.9rem;
}
.test-right>:nth-child(2){
    position: absolute;
    width: 17rem;
    height: 19rem;
    right: 7rem;
    top: 4rem;
    background: var(--planCard);
}

.arrows{
    display: flex;
    gap: 1rem;
    position: absolute;
    bottom: 1rem;
    left: 3rem;
}
.arrows>img{
     width: 1.5rem;
     cursor: pointer;
}

/* responsive */
@media screen and (max-width:768px){
    .testimonials{
        flex-direction: column;
    }
    .test-right{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 2rem;
    }
    .test-right>div{
        display: none;
    }
    .test-right>img{
        right: 0;
        position: relative;
        align-self: center;
    }
    .test-right>.arrows{
        position: relative;
        display: block;
        padding-top: 10%;
        padding-right: 35%;
       
        
    }
}