.Programs{
    display: flex;
    flex-direction: column;
    gap:2rem;
    padding: 0 2rem;
}

.programs-header{
    display: flex;
    gap: 5rem;
    font-weight: bold;
    font-size: 3rem;
    justify-content: center;
    color: white;
    text-transform: uppercase;
    font-style: italic;
}

.program-categories{
    display: flex;
    gap: 1rem;
}
.category{
    display: flex;
    flex-direction: column;
    background-color: gray;
    padding: 2rem;
    gap: 1rem;
    color: white;
    justify-content: space-between;
}
.category>:nth-child(1){
    width: 2rem;
    height: 2rem;
    /* fill: white; */
}
.category>:nth-child(2){
    font-size: 1.3rem;
    color: black;
    font-weight: bold;
    /* fill: white; */
}
.category>:nth-child(3){
    font-size: 1rem;
    line-height: 25px;
    /* fill: white; */
}

.category:hover{
    background: var(--planCard);
    cursor: pointer;
}

.join-now{
    display: flex;
    align-items: center;
    gap: 2rem;
}
.join-now>img{
    width: 1rem;
}

/* responsive */
@media screen and (max-width: 768px){
    .programs-header{
        flex-direction: column;
        gap: 1rem;
        font-size: xx-large;
        align-items: center;
        justify-content: center;
        margin-top: 2rem;
    }
    .program-categories{
        flex-direction: column;
    }
}